import React from 'react'
import { Typography, Paper, Container, Button, Grid } from '@material-ui/core'
import useStyles from '../hooks/useStyles'
import nalle from '../assets/kiekkonalle.png'
import TextField from './TextField'
import { useState } from 'react'
import axios from 'axios'

const FrontPage = () => {
  const tyylit = useStyles();
  const [ username, setUsername ] = useState('');
  const [ password, setPassword ] = useState('');

  const handleLogin = async () => {
    try {
    const token = await axios.post('/login', { username, password })
    console.log(token);
    window.localStorage.setItem('passuToken', token.data);
    } catch (e) {
      console.log('Väärä tunnus tai salasana');
    }
    setUsername('');
    setPassword('');
  }
  return (
    <Container maxWidth="md">
      <Paper style={tyylit.login} elevation={4}>
        <Typography variant="h4">Kirjaudu sisään</Typography>
        <Grid container direction="row" justifyContent="flex-start">
          <Grid item xs={6}>
            <img src={nalle} alt="Kiekkonalle" width="100%" />
          </Grid>
          <Grid item xs={6} style={{ marginTop: '10%', minWidth: 140 }}>
            <TextField placeholder="Tunnus..." name="username" fullWidth value={username} onChange={(e) => setUsername(e.target.value)} />
            <TextField placeholder="Salasana" name="password" fullWidth value={password} onChange={(e) => setPassword(e.target.value)} />
            <Button variant="contained" color="primary" onClick={handleLogin} >Kirjaudu</Button>
          </Grid>
        </Grid>

      </Paper>
    </Container>

  )
}
export default FrontPage