import { AppBar, Toolbar } from "@material-ui/core";
import NaviNappi from "./NaviNappi";

const Navipalkki = () => {
    return (
        <AppBar position="static">
            <Toolbar>
                <NaviNappi to="/">Etusivu</NaviNappi>
                <NaviNappi to="/points">Pisteet</NaviNappi>
                <NaviNappi to="/games">Pelit</NaviNappi>
                <NaviNappi to="/hc">HC</NaviNappi>
                <NaviNappi to="/upload">Lähetä .csv</NaviNappi>
            </Toolbar>
        </AppBar>
    )
}
export default Navipalkki;