import { Container, TableCell, TableBody, Paper, Table, TableContainer, TableHead, TableRow, Typography, Grid } from "@material-ui/core"
import axios from 'axios'
import { useEffect } from "react"
import { useState } from "react"
import useGetGames from "../hooks/useGetGames"

const pisteJako = [
    10, 6, 4, 3, 2, 1
]

const PointsList = () => {

    const [pisteet, setPisteet] = useState([])
    const [selected, setSelected] = useState(-1)
    const games = useGetGames();

    useEffect(() => {

        if (games === null || !games) return;
        const taulu = []
        for (const kisa of games) {
            for (const player of kisa.players) {
                let playerObj = taulu.find(p => p.name === player.name)
                if (!playerObj || playerObj === null) {
                    playerObj = { name: player.name, pisteet: 0, sijoitukset: [0, 0, 0, 0, 0, 0], pelit: 0 }
                    taulu.push(playerObj)
                }
                playerObj.pisteet += pisteJako[player.rankHC - 1] || 0
                playerObj.pelit++
                playerObj.sijoitukset[player.rankHC - 1]++
            }
        }
        setPisteet(taulu.sort((a, b) => b.pisteet - a.pisteet))
    }, [games])
    console.log(pisteet)
    return (
        <Container>
            <Typography variant="h4" gutterBottom>Pistetaulukko</Typography>
            <TableContainer component={Paper} style={{ borderRadius: 10, marginBottom: 30, maxWidth: 500 }}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Sija</TableCell>
                            <TableCell>Pelaaja</TableCell>
                            <TableCell>Pisteet</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {pisteet.map((p, i) => <PelaajanPisteet i={i} pelaaja={p} key={p.name} handleOnClick={setSelected} selected={selected} />)}
                    </TableBody>
                </Table>
            </TableContainer>
            <Typography variant="h5">Pisteiden jako</Typography>
            <TableContainer component={Paper} style={{ borderRadius: 20, maxWidth: 500 }}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell>Sija</TableCell>
                            <TableCell>Pisteet</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {pisteJako.map((p, i) => <TableRow hover="true"><TableCell>{i + 1}.</TableCell><TableCell>{p}</TableCell></TableRow>)}
                    </TableBody>
                </Table>
            </TableContainer>
        </Container>
    )
}
const PelaajanPisteet = ({ pelaaja, i, handleOnClick, selected }) => {
    return (
        <>
            <TableRow hover="true" onClick={() => handleOnClick(i)}>
                <TableCell>{i + 1}.</TableCell>
                <TableCell>{pelaaja.name}</TableCell>
                <TableCell>{pelaaja.pisteet}</TableCell>
            </TableRow>
            {(selected === i && <PelaajanTilastot pelaaja={pelaaja} />)}
        </>
    )
}
const PelaajanTilastot = ({ pelaaja }) => {
    return (
        <TableRow>
            <TableCell colSpan={3}>
                Voittoprosentti: <strong>{Math.round(pelaaja.sijoitukset[0] / pelaaja.pelit * 10000) / 100}</strong><br />
                Pelejä: <strong>{pelaaja.pelit}</strong>
                <Grid container direction="row" justifyContent="flex-start" style={{ width: 200, margin: 20 }}>
                    <>
                        <Grid item xs={6}><strong>Sijoitus</strong></Grid>
                        <Grid item xs={6}><strong>Kertoja</strong></Grid>
                    </>

                    {pelaaja.sijoitukset.map((s, i) =>
                        <>
                            <Grid item xs={6}>{i + 1}</Grid>
                            <Grid item xs={6}>{s}</Grid>
                        </>
                    )}
                </Grid>

            </TableCell>
        </TableRow>
    )

}
export default PointsList