import { Container, Typography } from "@material-ui/core";
import { useEffect } from "react";
import { useState } from "react";

import axios from 'axios'

const HandicapList = () => {

    const [hcList, setHcList] = useState([])

    useEffect(() => {
        axios.get( (process.env.NODE_ENV !== 'production') ?
            'http://localhost:3001/api/hc' :
            'https://risbeegomfkerho.herokuapp.com/api/hc'
            , { headers: { authorization: 'bearer ' + window.localStorage.getItem('passuToken')}}).then((res) => {
            setHcList(res.data)
        })
    }, [])
    return (
        <Container>
            <Typography variant="h3">Handicapit</Typography>
            <ul>
                {hcList.map(i => <Rata key={i.course.name+i.course.layout} rata={i} />)}
            </ul>
        </Container>
    )
}
const Rata = ({ rata }) => {
    return (
        <div>
            <li>
                {rata.course.name} / {rata.course.layout}
                <ul>
                    {rata.players.map(p => <Pelaaja key={p.name+rata.course.name+rata.course.layout} pelaaja={p} /> )}
                </ul>
            </li>
        </div>
    )
}
const Pelaaja = ( { pelaaja }) => {
    return (
        <li>
            <strong>{pelaaja.name}</strong>, pelejä: {pelaaja.games}, juokseva HC: {pelaaja.runningHc}, HC: <strong>{pelaaja.hc}</strong>
        </li>
    )
}
export default HandicapList;